function getRandomKey(dict) {
  const keys = Object.keys(dict);
  return keys[Math.floor(Math.random() * keys.length)];
}
export function pickRandomKey(obj, keys = []) {
  if (typeof obj === "string") return [obj, keys];
  let new_key = getRandomKey(obj);
  return pickRandomKey(obj[new_key], keys.concat([new_key]));
}

